import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import styles from './Input.module.scss';
import Label from '../Label/Label';
import ErrorSign from '../ErrorSign/ErrorSign';

const Input = props => (
  <div className={cn(styles['input-container'],
    {
      [styles['space-between']]: props.spaceBetween,
      [styles['margin-top']]: props.marginTop,
    })}
  >
    {
      props.label
      && (
        <Label
          text={props.text}
          isMandatory={props.isMandatory}
          htmlFor={props.id}
          hasError={props.hasError}
        />
      )
    }
    <div className={styles['input-wrapper']}>
      <input
        type={props.type}
        className={cn(styles.input, props.className, {
          [styles['has-error']]: props.hasError,
          [styles.placeholder]: props.value === '',
        })}
        name={props.name}
        placeholder={props.placeholder}
        onChange={props.onChange}
        value={props.value}
        id={props.id}
        min={props.min}
        max={props.max}
      />
      {
        props.inputText
        && (
        <span className={styles['input-text']}>
          {props.inputText}
        </span>
        )
      }
      {
        props.hasError
        && <ErrorSign errorMessage={props.errorMessage} />
      }
    </div>
  </div>
);

Input.propTypes = {
  label: PropTypes.bool,
  text: PropTypes.string,
  isMandatory: PropTypes.bool,
  type: PropTypes.string.isRequired,
  className: PropTypes.string,
  id: PropTypes.string.isRequired,
  name: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  min: PropTypes.string,
  max: PropTypes.string,
  inputText: PropTypes.string,
  spaceBetween: PropTypes.bool,
  marginTop: PropTypes.bool,
  hasError: PropTypes.bool,
  errorMessage: PropTypes.string,
};

Input.defaultProps = {
  label: true,
  text: '',
  className: 'styles.input',
  name: '',
  placeholder: '',
  value: '',
  min: '',
  max: '',
  isMandatory: false,
  inputText: '',
  spaceBetween: false,
  marginTop: true,
  hasError: false,
  errorMessage: '',
};

export default Input;
