import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import Label from '../Label/Label';
import styles from './Select.module.scss';

const Select = props => (
  <div className={styles['select-container']}>
    <Label
      text={props.text}
      isMandatory={props.isMandatory}
      htmlFor={props.id}
      labelShift={props.labelShift}
    />
    <div className={styles['select-wrapper']}>
      <select
        value={props.value}
        onChange={props.onChange}
        className={cn(styles.select, { [styles.placeholder]: props.value === '' })}
        id={props.id}
        name={props.name}
      >
        <option className={styles.placeholder} value="" disabled hidden>
          {props.placeholder}
        </option>
        {
          props.optgroups && props.optgroups.length
            ? props.optgroups.map(optgroup => (
              <optgroup key={optgroup.label} label={optgroup.label}>
                {optgroup.options.map(option => (
                  <option key={option.id} value={option.value} id={option.id}>
                    {`${optgroup.label} - ${option.label}`}
                  </option>
                ))}
              </optgroup>
            ))
            : props.options.map(option => (
              <option key={option.id} value={option.value} id={option.id}>
                {option.label}
              </option>
            ))
        }
      </select>
      <div className={styles.description}>{props.description}</div>
    </div>
  </div>
);

Select.propTypes = {
  text: PropTypes.string,
  isMandatory: PropTypes.bool,
  labelShift: PropTypes.string,
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  onChange: PropTypes.func.isRequired,
  optgroups: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    options: PropTypes.arrayOf(PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
      id: PropTypes.number,
    })),
  })),
  options: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    value: PropTypes.number,
    id: PropTypes.number,
  })),
  placeholder: PropTypes.string,
  description: PropTypes.string,
};

Select.defaultProps = {
  text: '',
  isMandatory: false,
  labelShift: '',
  optgroups: [],
  value: '',
  options: [],
  placeholder: 'Please choose',
  description: '',
};

export default Select;
