import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { aboutDict, errorsDict } from '../../../common/config/dict';
import FIELDS from '../utils';
import Label from '../../../common/components/Label/Label';
import RadioButton from '../../../common/components/Buttons/RadioButton/RadioButton';
import Input from '../../../common/components/Input/Input';
import styles from './Payment.module.scss';

const FREE_ID = 'free';
const PAID_ID = 'paid';

class Payment extends Component {
  constructor(props) {
    super(props);
    this.toggleChange = this.toggleChange.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.state = { [FIELDS.PAID]: false };
  }

  toggleChange() {
    this.setState(
      prevState => ({ [FIELDS.PAID]: !prevState[FIELDS.PAID] }),
      () => this.props.onEventPaidChange(this.state[FIELDS.PAID]),
    );
    if (!this.state[FIELDS.PAID]) {
      this.props.onEventFeeChange('');
    }
  }

  handleChange(event) {
    this.props.onEventFeeChange(event.target.value ? Number(event.target.value) : '');
  }

  render() {
    return (
      <div className={styles['payment-container']}>
        <Label
          text={this.props.text}
          isMandatory={this.state[FIELDS.PAID]}
          htmlFor={this.props.id}
          hasError={this.props.feeError && this.state[FIELDS.PAID]}
        />
        <form className={styles['buttons-container']}>
          <RadioButton
            type="radio"
            onChange={this.toggleChange}
            id={FREE_ID}
            value={false}
            checked={!this.state[FIELDS.PAID]}
            name={FIELDS.PAID}
            buttonText={aboutDict.payment.freeEvent}
          />
          <RadioButton
            type="radio"
            onChange={this.toggleChange}
            id={PAID_ID}
            value
            checked={this.state[FIELDS.PAID]}
            name={FIELDS.PAID}
            buttonText={aboutDict.payment.paidEvent}
          />
        </form>
        {
          this.state[FIELDS.PAID]
            && (
            <Input
              type="number"
              name={FIELDS.FEE}
              id={FIELDS.FEE}
              placeholder={aboutDict.payment.fee}
              value={this.props.feeValue}
              onChange={this.handleChange}
              className={styles['fee-input']}
              marginTop={false}
              inputText={aboutDict.payment.dollarSign}
              hasError={this.props.feeError}
              errorMessage={errorsDict.fee}
            />
            )
        }
      </div>
    );
  }
}

Payment.propTypes = {
  text: PropTypes.string,
  id: PropTypes.string,
  onEventPaidChange: PropTypes.func.isRequired,
  onEventFeeChange: PropTypes.func.isRequired,
  feeValue: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  feeError: PropTypes.bool,
};

Payment.defaultProps = {
  text: '',
  id: '',
  feeValue: 0,
  feeError: false,
};

export default Payment;
