import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import styles from './ErrorSign.module.scss';

const ErrorSign = props => (
  <div className={cn(styles.container, { [styles.center]: props.verticalAlignCenter })}>
    <div className={styles.arrow} />
    {props.errorMessage}
  </div>
);

ErrorSign.propTypes = {
  errorMessage: PropTypes.string.isRequired,
  verticalAlignCenter: PropTypes.bool,
};

ErrorSign.defaultProps = {
  verticalAlignCenter: true,
};

export default ErrorSign;
