import React, { Component } from 'react';
import PropTypes from 'prop-types';
import FormSection from '../../common/components/FormSection/FormSection';
import Input from '../../common/components/Input/Input';
import TextArea from '../../common/components/TextArea/TextArea';
import Select from '../../common/components/Select/Select';
import Payment from './Payment/Payment';
import StartsOn from './StartsOn/StartsOn';
import Button from '../../common/components/Buttons/Button/Button';
import {
  aboutDict, coordinatorDict, newEventFormDict, whenDict, errorsDict,
} from '../../common/config/dict';
import FIELDS from './utils';
import selectCategories from '../../common/config/categories';
import selectEmployees from '../../common/config/employes';
import rules from './validation/rules';
import styles from './NewEventForm.module.scss';

const prepareCategoryOptions = categories => categories.map(category => ({
  id: category.id,
  label: category.name,
  value: category.id,
}));

const isThereAnyError = errors => !!Object.keys(errors).find(errorKey => errors[errorKey]);

const createOptionFromCoordinator = coordinator => ({
  id: coordinator.id,
  value: coordinator.id,
  label: `${coordinator.name} ${coordinator.lastname}`,
});

const generateCoordinatorOptgroups = (employees) => {
  const me = employees.find(employee => employee.id === 3);
  const others = employees.filter(employee => employee.id !== 3);
  return [
    {
      label: coordinatorDict.me,
      options: [createOptionFromCoordinator(me)],
    },
    {
      label: coordinatorDict.others,
      options: others.map(other => createOptionFromCoordinator(other)),
    },
  ];
};

const initialState = {
  [FIELDS.TITLE]: '',
  [FIELDS.DESCRIPTION]: '',
  [FIELDS.CATEGORY]: '',
  [FIELDS.PAID]: false,
  [FIELDS.FEE]: '',
  [FIELDS.REWARD]: '',
  [FIELDS.DATE]: '',
  [FIELDS.DURATION]: 0,
  [FIELDS.COORDINATOR]: {
    [FIELDS.EMAIL]: '',
    [FIELDS.ID]: '3',
  },
  errors: {},
};

const convertTime = (time) => {
  let [hours, minutes] = time.split(':');
  hours = Number(hours) + 12;
  return `${hours}:${minutes}`;
};

const convertToHours = time => (time === 0 ? '' : time / 3600);

class NewEventForm extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleCoordinatorChange = this.handleCoordinatorChange.bind(this);
    this.handleEventPaidChange = this.handleEventPaidChange.bind(this);
    this.handleEventFeeChange = this.handleEventFeeChange.bind(this);
    this.handleDateChange = this.handleDateChange.bind(this);
    this.submitForm = this.submitForm.bind(this);
    this.state = initialState;
  }

  handleChange(event, type) {
    const newErrors = { ...this.state.errors, [event.target.name]: false };
    if (event.target.name === FIELDS.DURATION) {
      this.setState({ [event.target.name]: Number(event.target.value * 3600), errors: newErrors });
    } else if (type === 'number') {
      this.setState({ [event.target.name]: Number(event.target.value), errors: newErrors });
    } else {
      this.setState({ [event.target.name]: event.target.value, errors: newErrors });
    }
  }

  handleCoordinatorChange(newValue, isCoordinatorId) {
    this.setState((prevState) => {
      if (isCoordinatorId) {
        return {
          [FIELDS.COORDINATOR]: {
            [FIELDS.EMAIL]: prevState[FIELDS.COORDINATOR][FIELDS.EMAIL],
            [FIELDS.ID]: newValue,
          },
          errors: { ...prevState.errors, [FIELDS.EMAIL]: false, [FIELDS.ID]: false },
        };
      }
      return {
        [FIELDS.COORDINATOR]: {
          [FIELDS.EMAIL]: newValue,
          [FIELDS.ID]: prevState[FIELDS.COORDINATOR][FIELDS.ID],
        },
        errors: { ...prevState.errors, [FIELDS.EMAIL]: false, [FIELDS.ID]: false },
      };
    });
  }

  handleEventPaidChange(isEventPaid) {
    this.setState({ [FIELDS.PAID]: isEventPaid });
    if (isEventPaid === false) this.setState({ [FIELDS.FEE]: '' });
  }

  handleEventFeeChange(eventFee) {
    this.setState(prevState => ({
      [FIELDS.FEE]: eventFee,
      errors: { ...prevState.errors, [FIELDS.FEE]: false },
    }));
  }

  handleDateChange(date, time, dayPeriod) {
    const timeFormatted = dayPeriod === 'pm' ? convertTime(time, dayPeriod) : time;
    this.setState(prevState => ({
      [FIELDS.DATE]: `${date}T${timeFormatted}`,
      errors: { ...prevState.errors, [FIELDS.DATE]: false },
    }));
  }

  submitForm() {
    const errors = {};
    Object.keys(rules).forEach((ruleKey) => {
      const validationRule = rules[ruleKey];
      if (
        typeof validationRule === 'function'
        && !validationRule(this.state[ruleKey])
      ) {
        errors[ruleKey] = true;
      }
      if (typeof validationRule === 'object') {
        Object.keys(validationRule).forEach((nestedRuleKey) => {
          const nestedValidationRule = validationRule[nestedRuleKey];
          if (!nestedValidationRule(this.state[ruleKey][nestedRuleKey])) {
            errors[nestedRuleKey] = true;
          }
        });
      }
    });
    this.setState(
      { errors },
      () => !isThereAnyError(errors)
        && delete this.state.errors
        && this.props.onSubmitClick(this.state),
    );
  }

  render() {
    return (
      <div className={styles['new-event-form']}>
        <FormSection name={newEventFormDict.about}>
          <Input
            type="text"
            text={aboutDict.title}
            isMandatory
            placeholder={aboutDict.titlePlaceholder}
            onChange={this.handleChange}
            name={FIELDS.TITLE}
            value={this.state[FIELDS.TITLE]}
            id={FIELDS.TITLE}
            spaceBetween
            hasError={this.state.errors[FIELDS.TITLE]}
            errorMessage={errorsDict.title}
          />
          <TextArea
            text={aboutDict.description}
            isMandatory
            labelShift={styles['description-label-shift']}
            placeholder={aboutDict.descriptionPlaceholder}
            onChange={this.handleChange}
            name={FIELDS.DESCRIPTION}
            value={this.state[FIELDS.DESCRIPTION]}
            id={FIELDS.DESCRIPTION}
            maxLength="140"
            hasError={this.state.errors[FIELDS.DESCRIPTION]}
            errorMessage={errorsDict.description}
          />
          <Select
            text={aboutDict.category}
            labelShift={styles['category-label-shift']}
            placeholder={aboutDict.categoryPlaceholder}
            options={prepareCategoryOptions(selectCategories)}
            value={this.state[FIELDS.CATEGORY]}
            onChange={event => this.handleChange(event, 'number')}
            name={FIELDS.CATEGORY}
            id={FIELDS.CATEGORY}
            description={aboutDict.categoryDescription}
            hasError={this.state.errors[FIELDS.CATEGORY]}
          />
          <Payment
            text={aboutDict.payment.payment}
            onEventPaidChange={this.handleEventPaidChange}
            feeValue={this.state[FIELDS.FEE]}
            onEventFeeChange={this.handleEventFeeChange}
            hasError={this.state.errors[FIELDS.PAID]}
            feeError={this.state.errors[FIELDS.FEE]}
          />
          <Input
            type="number"
            className={styles['reward-input']}
            text={aboutDict.reward}
            placeholder={aboutDict.rewardPlaceholder}
            onChange={event => this.handleChange(event, 'number')}
            name={FIELDS.REWARD}
            value={this.state[FIELDS.REWARD]}
            id={FIELDS.REWARD}
            inputText={aboutDict.rewardText}
            hasError={this.state.errors[FIELDS.REWARD]}
            errorMessage={errorsDict.reward}
          />
        </FormSection>
        <FormSection name={newEventFormDict.coordinator}>
          <Select
            text={coordinatorDict.responsible}
            optgroups={generateCoordinatorOptgroups(selectEmployees)}
            value={this.state[FIELDS.COORDINATOR][FIELDS.ID]}
            onChange={event => this.handleCoordinatorChange(event.target.value, true)}
            name={FIELDS.ID}
            id={FIELDS.ID}
            isMandatory
            hasError={this.state.errors[FIELDS.ID]}
          />
          <Input
            type="email"
            text={coordinatorDict.email}
            isMandatory={false}
            placeholder={coordinatorDict.emailPlaceholder}
            onChange={event => this.handleCoordinatorChange(event.target.value)}
            name={FIELDS.EMAIL}
            value={this.state[FIELDS.COORDINATOR][FIELDS.EMAIL]}
            id={FIELDS.EMAIL}
            spaceBetween
            hasError={this.state.errors[FIELDS.EMAIL]}
            errorMessage={errorsDict.email}
          />
        </FormSection>
        <FormSection name={newEventFormDict.when}>
          <StartsOn
            onDateChange={this.handleDateChange}
            hasError={this.state.errors[FIELDS.DATE]}
            errorMessage={errorsDict.date}
          />
          <Input
            type="number"
            className={styles['duration-input']}
            text={whenDict.duration}
            isMandatory={false}
            placeholder={whenDict.durationPlaceholder}
            onChange={event => this.handleChange(event, 'number')}
            name={FIELDS.DURATION}
            value={convertToHours(this.state[FIELDS.DURATION])}
            id={FIELDS.DURATION}
            inputText={whenDict.durationText}
            hasError={this.state.errors[FIELDS.DURATION]}
            errorMessage={errorsDict.duration}
          />
        </FormSection>
        <Button
          className={styles['submit-button']}
          text={newEventFormDict.publishButton}
          onClick={this.submitForm}
        />
      </div>
    );
  }
}

NewEventForm.propTypes = {
  onSubmitClick: PropTypes.func,
};
NewEventForm.defaultProps = {
  onSubmitClick: () => true,
};

export default NewEventForm;
