import isEmail from 'validator/lib/isEmail';
import FIELDS from '../utils';

const validateTitle = val => val.length > 0;
const validateDescription = val => val.length > 0 && val.length <= 140;
const validateCategory = val => val.length === 0 || (val >= 0 && val <= 9);
const validatePaid = val => !val || typeof val === 'boolean';
const validateFee = val => val === '' || val > 0;
const validateReward = val => val.length === 0 || val > 0;
const validateDate = val => val === '' || (new Date(val) > new Date() && val !== '');
const validateDuration = val => !val || val > 0;
const validateMail = val => val === '' || isEmail(val);
const validateId = val => val >= 0 && val <= 9;

const rules = {
  [FIELDS.TITLE]: validateTitle,
  [FIELDS.DESCRIPTION]: validateDescription,
  [FIELDS.CATEGORY]: validateCategory,
  [FIELDS.PAID]: validatePaid,
  [FIELDS.FEE]: validateFee,
  [FIELDS.REWARD]: validateReward,
  [FIELDS.DATE]: validateDate,
  [FIELDS.DURATION]: validateDuration,
  [FIELDS.COORDINATOR]: {
    [FIELDS.EMAIL]: validateMail,
    [FIELDS.ID]: validateId,
  },
};

export default rules;
