import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { whenDict } from '../../../common/config/dict';
import FIELDS from '../utils';
import styles from './StartsOn.module.scss';
import Input from '../../../common/components/Input/Input';
import RadioButton from '../../../common/components/Buttons/RadioButton/RadioButton';
import ErrorSign from '../../../common/components/ErrorSign/ErrorSign';

const TIME = 'time';
const AM = 'am';
const PM = 'pm';

const getActualDate = () => {
  const today = new Date();
  const year = today.getFullYear();
  const month = today.getMonth();
  const twoDigitsMonth = month <= 8 ? `0${month + 1}` : month + 1;
  const day = today.getDate();
  return `${year}-${twoDigitsMonth}-${day}`;
};

class StartsOn extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = {
      date: '',
      time: '',
      dayPeriod: AM,
    };
  }

  handleChange(event) {
    this.setState(
      { [event.target.name]: event.target.value },
      () => this.props.onDateChange(this.state.date, this.state.time, this.state.dayPeriod),
    );
  }

  render() {
    return (
      <div className={styles.container}>
        <Input
          type="date"
          className={styles['date-input']}
          text={whenDict.startsOn}
          onChange={this.handleChange}
          name={FIELDS.DATE}
          value={this.state.date}
          placeholder="yy/ss"
          id={FIELDS.DATE}
          inputText={whenDict.at}
          marginTop={false}
          min={getActualDate()}
        />
        <Input
          label={false}
          type="time"
          className={styles['time-input']}
          onChange={this.handleChange}
          name={TIME}
          value={this.state.time}
          min="01:00"
          max="12:59"
          id={TIME}
          marginTop={false}
        />
        <form className={styles['time-form']}>
          <RadioButton
            type="radio"
            id={AM}
            value={AM}
            name="dayPeriod"
            checked={this.state.dayPeriod === AM}
            buttonText={whenDict.am}
            onChange={this.handleChange}
          />
          <RadioButton
            type="radio"
            id={PM}
            value={PM}
            name="dayPeriod"
            checked={this.state.dayPeriod === PM}
            buttonText={whenDict.pm}
            onChange={this.handleChange}
          />
        </form>
        <div className={styles.error}>
          {
            this.props.hasError
            && <ErrorSign errorMessage={this.props.errorMessage} />
          }
        </div>
      </div>
    );
  }
}

StartsOn.propTypes = {
  onDateChange: PropTypes.func.isRequired,
  hasError: PropTypes.bool,
  errorMessage: PropTypes.string,
};
StartsOn.defaultProps = {
  hasError: false,
  errorMessage: '',
};

export default StartsOn;
