import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import styles from './TextArea.module.scss';
import { aboutDict } from '../../config/dict';
import Label from '../Label/Label';
import ErrorSign from '../ErrorSign/ErrorSign';

const TextArea = props => (
  <div className={styles.container}>
    <Label
      text={props.text}
      isMandatory={props.isMandatory}
      htmlFor={props.id}
      hasError={props.hasError}
      labelShift={props.labelShift}
    />
    <div className={styles['textarea-wrapper']}>
      <textarea
        className={cn(styles.textarea, { [styles['has-error']]: props.hasError })}
        name={props.name}
        placeholder={props.placeholder}
        onChange={props.onChange}
        value={props.value}
        maxLength={props.maxLength}
      />
      <div className={styles['char-limit']}>
        {aboutDict.charLimit}
        <span className={styles['char-counter']}>
          {props.value.length}
          /140
        </span>
      </div>
      {
        props.hasError
        && <ErrorSign errorMessage={props.errorMessage} verticalAlignCenter={false} />
      }
    </div>
  </div>
);

TextArea.propTypes = {
  text: PropTypes.string,
  isMandatory: PropTypes.bool,
  id: PropTypes.string.isRequired,
  labelShift: PropTypes.string,
  name: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string,
  maxLength: PropTypes.string,
  hasError: PropTypes.bool,
  errorMessage: PropTypes.string,
};

TextArea.defaultProps = {
  text: '',
  labelShift: '',
  name: '',
  placeholder: '',
  value: '',
  isMandatory: false,
  maxLength: '',
  hasError: false,
  errorMessage: '',
};

export default TextArea;
