import React, { Component } from 'react';
import Header from './common/components/Header/Header';
import NewEventForm from './screens/NewEventForm/NewEventForm';
import FormSummary from './screens/FormSummary/FormSummary';

class App extends Component {
  constructor(props) {
    super(props);
    this.handleSubmitClick = this.handleSubmitClick.bind(this);
    this.state = {
      currentScreenId: 'form',
    };
  }

  handleSubmitClick(formData) {
    this.setState({ currentScreenId: 'summary' });
    const { errors, ...result } = formData;
    console.log(result);
  }

  render() {
    return (
      <div>
        <Header />
        <main>
          {
            this.state.currentScreenId === 'form'
              ? (
                <NewEventForm
                  onSubmitClick={this.handleSubmitClick}
                />
              )
              : <FormSummary />
          }
        </main>
      </div>
    );
  }
}

export default App;
