import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { labelDict } from '../../config/dict';
import styles from './Label.module.scss';

const Label = props => (
  <label
    className={cn(styles.label, props.labelShift, { [styles['has-error']]: props.hasError })}
    htmlFor={props.htmlFor}
  >
    {props.text}
    {
      props.isMandatory && (
        <sup className={styles['mandatory-sign']}>{labelDict.mandatorySign}</sup>
      )
    }
  </label>
);

Label.propTypes = {
  htmlFor: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
  isMandatory: PropTypes.bool,
  hasError: PropTypes.bool,
  labelShift: PropTypes.string,
};

Label.defaultProps = {
  isMandatory: false,
  hasError: false,
  labelShift: '',
};

export default Label;
